.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wrapper-game {
  /* border: dashed 1px whitesmoke; */
  font-family: "Press Start 2P", cursive;
  padding: 16px;
}

.d-flex {
  display: flex;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-between {
  justify-content: space-between;
}

.align-item-center {
  align-items: center;
}

.text-center {
  text-align: center;
}

.card-img {
  width: 55px;
  height: 55px;
  border-radius: 8px;
  border: solid 1px silver;
  padding: 8px;
  margin: 12px;
}

.card-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.start-button {
  text-align: center;
  font-weight: bold;
  padding: 12px 6px 12px;
  background-color: lightgrey;
  text-shadow: -1px -1px black, 1px 1px whitesmoke;
  color: grey;
  box-shadow: 0 0.2em gray;
  cursor: pointer;
  height: fit-content;
  border-radius: 8px;
  margin: 5px;
}

.start-button:active {
  box-shadow: none;
  position: relative;
  top: 0.2em;
}

.card-score {
  width: 65px;
  padding: 6px;
  border: 3px dashed palevioletred;
  background-color: whitesmoke;
  color: palevioletred;
  text-align: center;
  border-radius: 8px;
  text-shadow: 1px 1px 1px brown;
}

.icon-game {
  opacity: 0.5;
  cursor: pointer;
}

.icon-game:hover,
.active {
  opacity: 1;
  box-shadow: 0 0 10px 5px whitesmoke;
}
